<template>
  <v-touch :swipe-options="{ direction: 'all'}" @swipe="onSwipe">
  <div id="swipe-container" :style="{'background-position': rollingOffset}">
    <svg id="swipe-ui" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100">
      <g id="RollingHud">
        <radialGradient id="SVGID_1_" cx="50" cy="50" r="45" gradientUnits="userSpaceOnUse">
          <stop offset=".0" stop-color="#0000" />
          <stop offset=".44" stop-color="#0000" />
          <stop offset=".45" />
          <stop offset=".50" stop-color="#000f"/>
          <stop offset=".56" stop-color="#000000a3"/>
          <stop offset=".81" stop-color="#0000002f"/>
          <stop offset="1" stop-color="#0000"/>
        </radialGradient>

        <circle stroke="black" cx="50" cy="50" r="45" fill="url(#SVGID_1_)"/>
        <text id="CenterEmoji" x="30" y="60" font-size="35">🦍</text>
    </g>
    <g id="RollingTarget" :style="{transform: targetTransform}" fill="none" stroke-miterlimit="10" >
      <circle stroke-width="3" stroke="#0071BC" cx="50" cy="50" r="7.5"/>
      <path stroke-width="3" stroke="#0071BC" d="M42.5 50h15M50 57.5v-15"/>
    </g>
    </svg>
  </div>
  <h1>{{highestVelocity}} Highest Velocity</h1>
  </v-touch>
</template>

<script>
export default {
  name: 'app',
  methods: {
    onSwipe($event){
      console.log("Swipe?", $event);
      this.highestVelocity = Math.max(this.highestVelocity, Math.abs($event.velocity));
      //Position the Target
      this.targetTransform = `rotate(${$event.angle}deg) translateX(${20+Math.min(20,Math.abs($event.velocity)*5)}%)`;
      //Calculate drag offset for background
      this.offset.x -= $event.deltaX;
      this.offset.y -= $event.deltaY;
      this.rollingOffset = `${this.offset.x}px ${this.offset.y}px`;
    }
  },
  data () {
    return {
      targetTransform: "",
      rollingOffset: "",
      offset: {x:0, y:0},
      highestVelocity: 0
    }
  }
}
</script>

<style>
#swipe-container {
  height: 90vh;
  width: 90vw;
  align-self: center;
  margin: 0;
  padding: 0;
  background-image: url(./assets/topography.svg);
  background-repeat: repeat;
  transition: background-position 1s ease;
  position: absolute;
  left: 4vw;
}

#swipe-ui {
  height: 200px;
  width: 200px;
  display: block;
  position: absolute;
  left: calc(50% - 100px);
  top: calc(50% - 100px);
}

#app {
}

#RollingTarget {
  transform-origin: center;
}

</style>



